@charset "UTF-8";

:root {
    --button-color: rgb(15, 111, 221);
    --header-height: 60px;
}

.container {
    width: min(100% - 40px, 1080px);
    margin-inline: auto;
}

body {
    font-family: "Inter", Arial, sans-serif;
    color: rgb(255, 255, 255);
}

.flex {
    display: flex;
}

.sb {
    justify-content: space-between;
}

.gap {
    gap: 20px;
}

/* MAIN */

main {
    padding: 140px 0;
}

h1 {
    text-align: center;
    font-size: 42px;
    font-weight: 500;
    margin-bottom: 16px;
    letter-spacing: 0.5px;
}

form { 
    display: flex;
    justify-content: center;
}

input[type=text] {
    width: 350px;
    height: 42px;
    margin-right: 10px;
    border-radius: 5px;
    background-color: rgba(255, 255, 255, 0.03);
    padding: 0 15px;
    font-size: 14px;
    color: rgba(255, 255, 255, 0.808);
    border: 1px solid rgba(255, 255, 255, 0.02);
    transition-duration: 0.2s;
    &:focus {
        border-color: rgba(255, 255, 255, 0.06);
    }
}

.add-task {
    height: 42px;
    border-radius: 5px;
    font-size: 14px;
    width: 100px;
    font-weight: 500;
    background-color: var(--button-color);
    color: rgb(255, 255, 255);
    transition-duration: 0.2s;
    &:hover {
        opacity: 0.8;
    }
    &:active {
        opacity: 0.6;
    }
}

.grid {
    width: 100%;
    margin-top: 100px;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 12px; 
}

.task {
    background: linear-gradient(to right, rgba(255, 255, 255, 0.03), rgba(255, 255, 255, 0.04));
    height: 90px;
    border-radius: 2px;
    border-left: 2px solid rgba(52, 211, 52, 0.637);
    padding: 13px 20px;
    overflow: hidden;
}

.task div:first-child {
    height: 100%;
    display: grid;
    place-items: center;
    overflow-y: scroll;
}

.task div:first-child::-webkit-scrollbar {
    display: none;
}

.task h2 {
    font-size: 22px;
    font-weight: 400;
    width: 100%;
    text-align: left;
}

.task p {
    width: 100%;
    font-size: 12px;
    opacity: 0.4;
    text-align: left;
}

.icons {
    display: flex;
    justify-content: space-between;
    align-items: flex-end;
    padding: 5px 0;
}

i {
    font-size: 13px;
    transition-duration: 0.2s;
    cursor: pointer;
    opacity: 0.6;
}

i + i {
    margin-left: 12px;
}

.fa-pen:hover {
    color: rgb(35, 187, 35);
}

.fa-trash:hover {
    color: rgb(255, 0, 0);
}